import { model, Schema, models } from 'mongoose';

export default function getstockModel() {
  const collectionName = 'stock';
  if (models[collectionName]) {
    return model(collectionName);
  }
  const stockSchema = new Schema({
    productCode: {
      type: String,
      required: true,
    },
    brandName: {
      type: String,
      required: true,
    },
    stockType: {
      type: String,
      required: true,
    },
    productSize: {
      type: String,
      required: true,
    },
    production: {
      type: String,
      required: true,
    },
    sales: {
      type: Number,
      required: true,
    },
    openingStock: {
      type: Number,
      required: true,
    },
    closingStock: {
      type: Number,
      required: true,
    },
    breakage: {
      type: Number,
      required: true,
    },
    shortage: {
      type: Number,
      required: true,
    },
    date: {
      type: Date,
    },
  });

  return model(collectionName, stockSchema);
}
